/**
 * @typedef {object} opts
 * @param {object} opts.endpoints
 * @param {string} opts.baseUrl
 * @param {string} opts.apiKey
 * @param {string?} opts.apiHeaderKey
 * @param {string?} opts.defaultMethod
 * @param {object?} opts.baseHeaders
 * @param {function | string} opts.token
 * @param {function?} opts.fetch
 */

const {
    DEFAULT_METHOD,
    API_HEADER_KEY
} = require('./constants')

/**
 * @class FetchDog
 */

class FetchDog {

    /**
     * @param opts
     */
    constructor(opts) {

        const baseHeaders = {
            'content-type': 'application/json'
        }

        this._fetch = opts.fetch || fetch
        this._endpoints = opts.endpoints
        this._baseUrl = opts.baseUrl
        this._baseHeaders = opts.baseHeaders || baseHeaders
        this._defaultMethod = opts.defaultMethod || DEFAULT_METHOD
        this._token = opts.token
        this._baseHeaders[opts.apiHeaderKey || API_HEADER_KEY] = opts.apiKey

    }

    /**
     * @description cleanup trailing or end slashes|backslashes from url definition
     * @param {string} url
     * @param {boolean?} start
     * @return {string}
     * @private
     */
    _cleanUrl(url, start = false) {
        if (url.length < 2) return url
        const hasSlash = arg => arg === '/' || arg === '\\'
        let tmp = url
        if (!start && hasSlash(url[0])) tmp = url.substr(1)
        if (hasSlash(url[url.length - 1])) tmp = tmp.substr(0, tmp.length - 1)
        return tmp
    }

    /**
     * @param {...string} args
     * @returns {string}
     * @private
     */
    _url(...args) {
        return args.map(
            (a, idx) =>
                this._cleanUrl(a, idx === 0)
        ).join('/')
    }

    /**
     * @description configure authorization header
     * @param {string} token
     * @returns {string}
     * @private
     */
    _bearer(token) {
        return `bearer ${token}`
    }

    _getAuthHeader(endpointKey){
        const token = typeof this._token === 'function'
            ? this._token()
            : this._token
        return {
            ...this._baseHeaders,
            authorisation: this._bearer(this._token())
        }
    }

    /**
     * @description set authorization headers if this is an authenticated endpoint
     * @param {string} endpointKey
     * @private
     * @return {object}
     */
    _auth(endpointKey) {
        if (!this._endpoints[endpointKey].auth) return this._baseHeaders
        return this._getAuthHeader(endpointKey)
    }

    /**
     * @description build the endpoint url
     * @param {string} endpointKey
     * @returns {string}
     * @private
     */
    _endpoint(endpointKey) {
        return this._url(
            this._baseUrl,
            this._endpoints[endpointKey].url)
    }

    /**
     * @description public fetch method
     * @param {string} endpointKey
     * @param {object?} payload
     * @param {string?} method
     * @param {string?} urlParams
     * @return {Promise<Object>}
     */
    async execute(endpointKey, payload, method, urlParams) {
        try {
            const httpRes = await this._fetch({
                url: this._url(this._endpoint(endpointKey), urlParams || ''),
                headers: this._auth(endpointKey),
                method: method || this._endpoints[endpointKey].method || this._defaultMethod,
                body: JSON.stringify(payload)
            })

            if(httpRes.ok) return await httpRes.json()

            return {
                status: httpRes.status,
                message: httpRes.statusText
            }

        } catch (err) {
            return err
        }
    }
}

module.exports = FetchDog