const FetchDog = require('./fetch-dog.class')
const {DEFAULT_ENDPOINT, DEFAULT_METHOD} = require('./constants')

class FetchDogGql extends FetchDog {

    constructor(opts){
        super(opts)
        this._defaultEndpoint = opts._defaultEndpoint || DEFAULT_ENDPOINT
        this._defaultMethod = opts.defaultMethod || DEFAULT_METHOD
        this._nonGqlcall = this.execute
        this.execute = this._execute
    }

    static _isGqlCall(inStr){
        return inStr.indexOf('}') !== -1
    }

    async _gqlExecute(query){
        const url = this._endpoint(this._defaultEndpoint)
        const headers = this._getAuthHeader(this._defaultEndpoint)
        const method = this._defaultMethod
        const body = query
    }

    _execute(endpoint, payload, method){
        if(FetchDogGql._isGqlCall(endpoint))
            return this._gqlExecute(endpoint)
        return this._nonGqlcall(endpoint, payload)
    }
}